import React from 'react';
import {Link,NavLink} from 'react-router-dom';

const Contact=()=>{
return(

<div>
	<nav className="ui raised very padded segment">
	<span style={{fontSize:'25px'}} ><Link to="/">Asfak Ahmed</Link></span>
	<div className="ui right floated header">
		<button className="ui button"><Link to="/">About</Link></button>
		<button className="ui button"><NavLink to="/Projects">Projects</NavLink></button>
		<button className="ui yellow button"><NavLink to="/Contact">Contact</NavLink></button>
	</div>
	</nav>
	
   <div className="ui raised very padded text container segment"style={{marginTop:'80px'}}>
	<Link  to='/Phone' ><p style={{textAlign:'center'}} className='ui text segment' >Click for Phone no.</p></Link>
	<Link to='/Email'><p style={{textAlign:'center'}} className='ui text segment' >Click for Email</p></Link>
   </div>	
</div>
)
}
export default Contact;